  text-align: center;
  width: 200px;
  height: 100px;
  font-size: xx-large;
  font-weight: bold;
  padding: 5px;
  position: relative;
  border-radius: 0.2em;
  border: 0;
  margin: auto;
  background-color: #fff800;
  color:black;
  text-decoration: none;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
      cursor: pointer;  
}

#bingo{
  display: flex;
  justify-items: center;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 200px;
  height: 100px;
  font-size: xx-large;
  font-weight: bold;
  padding: 5px;
  position: relative;
  border-radius: 0.2em;
  border: 0;
  margin: auto;
  color:black;
  text-decoration: none;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
      cursor: pointer;  
}

.bingo.disabled {
  opacity: 0.9; 
  cursor: not-allowed; 
  background-color: grey;
}

.bingo {
  background-color: #fff800;
}

img.ticker-tape{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 160%;
  object-fit: cover;
}


.how-to-use-app{
margin-top: 20px;
}

.grid-item
{
  border: none;
  outline: none;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  margin: 10px 10px 10px 10px;
}

.grid-item:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.grid-item:active {
  color: #000
}
.grid-item.active  > .euro-btn{
top: 2px;
left: 1px;
box-shadow: none;
background-color: #231557;
color: whitesmoke;
}

.grid-item:active:after {
  background: transparent;
}

.grid-item:hover:before {
  opacity: 1;
}

.grid-item:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.animated-header{
 align-items: center;
 justify-content: center;
 margin-bottom: 5px;
}

header {
  margin-top: 5px;
}

.animate-character
{
   text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: 95% 95%;
  background-clip: border-box;
  background-size: 50% 50%;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
  display: inline-block;
      font-size: 120px;
      text-align: center;
}

.bingo-nav{
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: rgb(255, 255, 255, 0.6);
  height: 30px;
}


